.text-input {
    cursor:text;
    margin:auto;
    padding: 11px;
    padding-left: 15px;
    padding-right: 3rem;
    border: 0;
    border-radius: 20px;
    box-shadow:0px 1px 9px rgba(0, 0, 0, 0.21);
    width:22rem;
    transition: all .1s ease-in;
}

.text-input:focus {
    border: 3px solid var(--accent-color);
    outline:none
}

@media screen and (max-width:500px) {
    .textInput {
        width: 15rem;
    }
}